:root {
  /* HSL Color Palette */
  --bg-dark: 240 10% 4%;
  /* #09090b */
  --bg-card: 240 10% 8%;
  /* #141417 */
  --bg-card-hover: 240 10% 12%;

  --primary: 244 97% 61%;
  /* #483bfc */
  --primary-glow: 244 97% 61%;
  --secondary: 190 100% 50%;
  /* Cyan accent */

  --text-main: 0 0% 100%;
  --text-muted: 240 5% 65%;
  --border: 240 6% 15%;

  --glass-bg: rgba(255, 255, 255, 0.03);
  --glass-border: rgba(255, 255, 255, 0.08);
  --glass-shine: rgba(255, 255, 255, 0.05);

  --font-sans: 'Inter', system-ui, sans-serif;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: hsl(var(--bg-dark));
  color: hsl(var(--text-main));
  font-family: var(--font-sans);
  min-height: 100vh;
  overflow-x: hidden;
  background-image:
    radial-gradient(circle at top center, rgba(91, 78, 255, 0.1) 0%, transparent 18%),
    radial-gradient(circle at 15% 50%, rgba(72, 59, 252, 0.03), transparent 40%),
    radial-gradient(circle at 85% 30%, rgba(6, 182, 212, 0.03), transparent 25%);
}

a {
  color: inherit;
  text-decoration: none;
}

/* Utilities */
.glass-panel {
  background: var(--glass-bg);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid var(--glass-border);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
}

.btn-primary {
  background: linear-gradient(135deg, hsl(var(--primary)), hsl(260, 80%, 60%));
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(72, 59, 252, 0.3);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 0 30px rgba(72, 59, 252, 0.5);
}

.input-field {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid var(--border);
  padding: 0.8rem 1rem;
  border-radius: 8px;
  color: white;
  width: 100%;
  font-size: 1rem;
  transition: border-color 0.2s;
}

.input-field:focus {
  outline: none;
  border-color: hsl(var(--primary));
  box-shadow: 0 0 0 2px rgba(72, 59, 252, 0.2);
}

.title-gradient {
  background: linear-gradient(to right, #fff, #94a3b8);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-fade-in-up {
  animation: fadeInUp 0.6s ease-out forwards;

}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #09090b;
}

::-webkit-scrollbar-thumb {
  background: #27272a;
  border-radius: 5px;
  border: 2px solid #09090b;
  /* Padding effect */
}

::-webkit-scrollbar-thumb:hover {
  background: #3f3f46;
}

